import { addLog } from '../utils/applogs';

export const loadSensors = () => {
  addLog('loadSensors()');
  return fetch(`https://3gzxlzr6d4.execute-api.us-east-1.amazonaws.com/dev/sensor`)
    .then(response => response.json())
    .then(data => {
      console.log('loadSensors(1)', data);
      return data.sensors;
    })
    .catch((error) => {
      console.error('ERROR', error);
    });
}

export const updateSensor = ({ sensorId, isFavourite }) => {
  addLog('unmarkSensorAsFavourite: ', sensorId);
  return fetch(`https://3gzxlzr6d4.execute-api.us-east-1.amazonaws.com/dev/sensor/${sensorId}`,
    {
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      method: 'PATCH',
      body: JSON.stringify({ isFavourite })
    }
  )
    .then(response => response.json())
    .catch((error) => {
      console.error('ERROR', error);
    });
};

const groupBy = (array, prop) => {
  return array.reduce(function (groups, item) {
    const val = item[prop]
    groups[val] = groups[val] || []
    groups[val].push(item)
    return groups
  }, {})
}

